.edit {
  width: 100%;
  display: flex;

  .editContainer {
    flex: 6;
  }


  .topEdit, .bottomEdit {
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    padding: 10px;
    margin: 20px;
    display: flex;

    h1 {
      color: lightgray;
      font-size: 20px;
    }

    .left {
      flex: 1;
      text-align: center;
      justify-self: center;
      align-self: center;

      img {
        width: 150px;
        height: 150px;
        border-radius: 50%;
        object-fit: cover;

        &:hover {
          cursor: pointer;
          opacity: 0.8;
        }
      }
    }

    .right {
      flex: 2;

      form {
        display: flex;
        flex-wrap: wrap;
        gap: 30px;
        justify-content: space-around;

        .formInput {
          width: 40%;

          label {
            display: flex;
            align-items: center;
            gap: 10px;

            .icon {
              cursor: pointer;
            }
          }

          input {
            width: 100%;
            padding: 5px;
            border: none;
            border-bottom: 1px solid gray;
          }
        }

        button {
          width: 150px;
          padding: 10px;
          border: none;
          border-radius: 5px;
          background-color: teal;
          color: white;
          font-weight: bold;
          cursor: pointer;
          margin-top: 10px;

          &:hover {
            background-color: #025d5d;
          }

          &:disabled {
            background-color: #cef3f3;
            cursor: not-allowed;
          }
        }
      }
    }

  }
}